@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap);

html,
body {
	height: 100%;
	font-family: 'Poppins', sans-serif;
}

*,
*::before,
*::after {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-size: 100%;
	font-weight: normal;
}

a {
	text-decoration: none;
	color: inherit;
	background-color: transparent;
}

ul,
ol {
	list-style: none;
}

button,
input,
select,
textarea {
	font: inherit;
}

button,
[type="button"],
[type="reset"],
[type="submit"],
[role="button"] {
	cursor: pointer;
	color: inherit;
}

img,
video {
	display: block;
	max-width: 100%;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

td,
th {
	padding: 0;
}

td:not([align]),
th:not([align]) {
	text-align: left;
}
.sign-up {
  height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.sign-up form {
  width: 400px;
}

.sign-up form h1 {
  font-size: 2.2rem;
  font-weight: bold;
  text-align: center;
}

.sign-up form label,
input {
  display: block;
}

.sign-up form input {
  width: 100%;
  outline: none;
  border: 0;
  border-bottom: 2px solid lightgrey;
  padding: 7px 0;
}

.sign-up form label {
  margin-top: 25px;
  margin-bottom: 5px;
  color: grey;
}

.sign-up form button {
  width: 100%;
  margin-top: 20px;
  background-color: black;
  color: white;
  padding: 10px;
  border: none;
  border-radius: 7px;
}

.sign-up p {
  margin-top: 40px;
  color: grey;
}

.login-page-link {
  color: black;
  font-weight: bold;
}

.log-in {
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.log-in form {
  width: 400px;
}

.log-in form h1 {
  font-size: 2.2rem;
