.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
}

.navbar h1 {
  font-size: 1.5rem;
  font-weight: bold;
}

.navbar input {
  padding: 7px 3px;
  border: 2px solid grey;
  border-radius: 7px;
  outline: none;
}

.navbar input::-webkit-input-placeholder {
  text-align: center;
}

.navbar input:-ms-input-placeholder {
  text-align: center;
}

.navbar input::placeholder {
  text-align: center;
}

.navbar button {
  background-color: black;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 7px;
}

@media (min-width: 768px) {
  .navbar input {
    width: 300px;
  }
}

@media (min-width: 992px) {
  .navbar input {
    width: 350px;
  }
}

@media (min-width: 1200px) {
  .navbar input {
    width: 400px;
  }
}

.note {
  background-color: rgb(226, 222, 222);
  padding: 20px 20px;
  border-radius: 7px;
}

.notes .note h1 {
  font-size: 1.25rem;
  font-weight: bold;
}

.notes {
  padding: 10px 30px;
}

@media (min-width: 576px) {
  .notes {
    padding: 10px 70px;
  }
}

@media (min-width: 768px) {
  .notes {
    padding: 10px 100px;
  }
}

@media (min-width: 1200px) {
  .notes {
    padding: 10px 150px;
  }
}

.modal {
  background-color: rgba(0, 0, 0, 0.2);
  width: 100vw;
  height: 100vh;
  z-index: 0;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(calc(-50% - 0.5px), calc(-50% - 0.5px));
          transform: translate(calc(-50% - 0.5px), calc(-50% - 0.5px));
  position: absolute;
}

.modal__center {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(calc(-50% - 0.5px), calc(-50% - 0.5px));
          transform: translate(calc(-50% - 0.5px), calc(-50% - 0.5px));
}

.modal .modal__content {
  height: 590px;
  width: 550px;
  z-index: 10;
  overflow: auto;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  background-color: white;
}

.modal .modal__content h1 {
  font-size: 1.25rem;
  font-weight: bold;
}

.modal__buttons {
  background-color: white;
  height: 40px;
  width: 550px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 15px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.modal__buttons button {
  width: 30px;
  height: 30px;
  background-color: white;
  color: black;
  border: none;
}

.modal__content .title {
  width: 100%;
  height: 10%;
  padding: 10px 15px;
  border: none;
  font-size: 1.25rem;
  font-weight: bold;
  outline: none;
}

.modal__content .description {
  width: 100%;
  height: 87%;
  padding: 0 15px;
  border: none;
  outline: none;
}

.modal__content ::-webkit-scrollbar {
  display: none;
}

.hidden {
  display: none;
}

.show {
  display: block;
}

.new-note-input {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 30px 0;
}

.new-note-input form {
  width: 400px;
}

.new-note-input form input {
  width: 100%;
  border-radius: 7px;
  border: 2px solid grey;
  outline: none;
  padding: 10px;
  margin-bottom: 15px;
}

.new-note-input form input::-webkit-input-placeholder {
  text-align: center;
}

.new-note-input form input:-ms-input-placeholder {
  text-align: center;
}

.new-note-input form input::placeholder {
  text-align: center;
}

.new-note-input form button {
  width: 100%;
  margin-top: 15px;
  padding: 10px;
  color: white;
  background-color: black;
  border: none;
  border-radius: 8px;
}

.new-note-input form textarea {
  border-radius: 7px;
  border: 2px solid grey;
  outline: none;
  padding: 10px;
}

.new-note-input form textarea::-webkit-input-placeholder {
  text-align: center;
}

.new-note-input form textarea:-ms-input-placeholder {
  text-align: center;
}

.new-note-input form textarea::placeholder {
  text-align: center;
}

